export class LongSegLine {
  /** åè¡¨ */
  public list: Array<ADItem[]>
  constructor() {
    this.list = []
  }
  /** æ·»å ä¸æ¡å¼¹å¹ */
  public add(dan: ADItem) {
    let startmin = Math.floor(dan.start / 60000)  // è·åå¼å§æ¶æå¨çåéæ®µ
    let endmin = Math.floor(dan.end / 60000)    // è·åç»ææ¶æå¨çåéæ®µ
    for (let m = startmin; m <= endmin; m++) {
      this.listAdd(m, dan) // å¼¹å¹å­å¨çä»»ä½é¿æ®µé½æè¯¥å¼¹å¹å¯¹è±¡çå¼ç¨
    }
  }
  /** å é¤ä¸æ¡å¼¹å¹ */
  public remove(dan: ADItem) {
    let startmin = Math.floor(dan.start / 60000)  // è·åå¼å§æ¶æå¨çåéæ®µ
    let endmin = Math.floor(dan.end / 60000)    // è·åç»ææ¶æå¨çåéæ®µ
    for (let m = startmin; m <= endmin; m++) {  // å¨è¯¥æ¶æ®µåéæ®µæ¥æ¾å¼¹å¹ä½ç½®å¹¶ä¾æ¬¡å é¤
        let i = this.list[m]?.indexOf(dan)
        i && i != -1 && this.list[m]?.splice(i, 1)
    }
  }
  /** è·åä¸ä¸ªåæ®µçå¼¹å¹ */
  public getSegList(i:number): ADItem[] {
    return this.listGet(i)
  }
  /** æ¸ç©ºæ¶é´è½´ */
  public clear() {
    this.list = []
  }
  private listAdd(i:number, item:any) {
    if (!this.list[i]) {
      this.list[i] = []
    }
    this.list[i]?.push(item)
  }
  private listGet(i:number): ADItem[] {
    return this.list[i] || []
  }
}