import { ADItem } from "../Danmaku/ADItem"

/**
 * ç­æ®µæ¶é´è½´
 * ä»¥1s=1000msä¸ºä¸ä¸ªç­æ®µ
 */

export class ShortSegLine {
  /** åè¡¨ */
  public list: Array<ADItem[]>
  constructor() {
    this.list = []
  }
  /** æ·»å ä¸æ¡å¼¹å¹ */
  public add(dan: ADItem) {
    let s = Math.floor(dan.start / 1000)  // è·åå¼å§æ¶æå¨çç§æ®µ
    this.listAdd(s, dan)      // å°å¼¹å¹æ·»å è¿å¯¹åºçç§æ®µä¸­
  }
  /** å é¤ä¸æ¡å¼¹å¹ */
  public remove(dan: ADItem) {
    let s = Math.floor(dan.start / 1000)  // è·åå¼å§æ¶æå¨çç§æ®µ
    let i = this.list[s]?.indexOf(dan)    // å¨è¯¥ç§æ®µåæ¥æ¾å¼¹å¹ä½ç½®å¹¶å é¤
    i && i != -1 && this.list[s]?.splice(i, 1)
  }
  /** è·åä¸ä¸ªåæ®µçå¼¹å¹ */
  public getSegList(i:number): ADItem[] {
    return this.listGet(i)
  }
  /** æ¸ç©ºæ¶é´è½´ */
  public clear() {
    this.list = []
  }
  private listAdd(i:number, item:any) {
    if (!this.list[i]) {
      this.list[i] = []
    }
    this.list[i].push(item)
  }
  private listGet(i:number): ADItem[] {
    return this.list[i] || []
  }
}